<template>
  <div class="log-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="fa fa-history" type="warning" size="small" @click="backupVisible = true"> {{$t('logBackup.backup')}}</el-button>
    </div>
    <el-form :model="backupForm" ref="backupForm" label-width="55px" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item :label="$t('logBackup.date') + '：'">
            <el-date-picker
              v-model="backupForm.dateTime"
              type="date"
              value-format="yyyy-MM-dd"
              :placeholder="$t('common.selectDate')">
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" icon="el-icon-search" size="small" @click="searchBackup">{{$t('common.search')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-table :data="backupList" stripe>
      <el-table-column
        prop="name"
        :label="$t('logBackup.name')">
      </el-table-column>
      <el-table-column
        prop="startTimeStr"
        :label="$t('logBackup.startTime')">
      </el-table-column>
      <el-table-column
        prop="endTimeStr"
        :label="$t('logBackup.endTime')">
      </el-table-column>
      <el-table-column
        prop="path"
        :label="$t('logBackup.directory')">
      </el-table-column>
      <el-table-column
        prop="size"
        label="备份大小"
        width="140"
        align="center">
      </el-table-column>
      <el-table-column
        :label="$t('common.operation')">
        <template slot-scope="props">
          <el-button type="text" size="mini" icon="el-icon-refresh" @click="backupReset(props.row)">{{$t('logBackup.reduction')}}</el-button>
          <el-button type="text" class="btn-text-red" size="mini" icon="el-icon-delete"
                     @click="deleteBackup(props.$index, props.row)">{{$t('common.delete')}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="totalPage">
    </el-pagination>
    <el-dialog
      :title="$t('logBackup.backupLog')"
      :visible.sync="backupVisible"
      width="30%"
      @close="backupCancel('backupLog')"
      center>
      <el-form :model="backupLog" :rules="backlogRules" ref="backupLog" label-width="80px">
        <el-form-item :label="$t('logBackup.date')" prop="dateTime">
          <el-date-picker
            v-model="backupLog.dateTime"
            type="date"
            value-format="yyyy-MM-dd"
            :placeholder="$t('common.selectDate')">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" :loading="loading" @click="backupOk('backupLog')">{{ $t('common.ok') }}</el-button>
        <el-button @click="backupCancel('backupLog')">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        currentPage: 1, // 当前页码
        totalPage: 0, // 总页数
        pageSize: 10, // 每页条数
        backupVisible: false, // 控制备份弹窗显示隐藏
        backupName: '', // 备份名称
        backupForm: { // 列表筛选表单
          dateTime: ''
        },
        backupLog: { // 弹窗表单
          dateTime: ''
        },
        backlogRules: { // 弹窗表单校验规则
          dateTime: [
            {required: true, message: '请选择时间', trigger: 'blur'}
          ]
        },
        loading: false,
        paramsForm: {} // 接口参数
      }
    },
    computed: {
      ...mapState({
        backupList: state => state.log.backupList
      })
    },
    activated() {
      const vm = this
      vm.searchBackup()
    },
    methods: {
      ...mapActions('log', ['searchLogBackup', 'backupLogData', 'backupLogReset', 'backupLogDelete']),
      handleSizeChange(val) {
        const vm = this
        vm.pageSize = val
        vm.searchBackup()
      },
      handleCurrentChange(val) {
        const vm = this
        vm.currentPage = val
        vm.searchBackup()
      },
      // 查询备份
      searchBackup() {
        const vm = this
        vm.backupForm.dateTime && (vm.currentPage = 1)
        vm.paramsForm = Object.assign({
          pageNo: vm.currentPage,
          pageSize: vm.pageSize
        }, vm.backupForm)

        vm.searchLogBackup(vm)
      },
      // 删除备份
      deleteBackup(index, backup) {
        const vm = this
        vm.$confirm(`是否确认删除备份：${vm.backupList[index].name}？`, '删除', {
          confirmButtonText: vm.$t('common.ok'),
          cancelButtonText: vm.$t('common.cancel'),
          type: 'warning',
          center: true
        }).then(async () => {
          vm.backupName = backup.name
          const response = await vm.backupLogDelete(vm)
          console.log(response)
          vm.backupList.splice(index, 1)
          vm.$message({
            type: 'success',
            message: '删除成功！'
          })
        }).catch(() => {
        })
      },
      // 确认备份
      backupOk: function(formName) {
        const vm = this
        vm.$refs[formName].validate(valid => {
          if (valid) {
            vm.backupLogData(vm)
          }
        })
      },
      // 备份弹窗取消
      backupCancel(formName) {
        const vm = this
        vm.backupVisible = false
        vm.$refs[formName].clearValidate()
        vm.$refs[formName].resetFields()
      },
      // 备份还原
      backupReset(backup) {
        const vm = this
        vm.backupName = backup.name
        vm.backupLogReset(vm)
      }
    }
  }
</script>

<style lang="scss">
  .log-main {
    .dialog-footer .el-icon-loading {
      font-size: 14px;
    }
  }
</style>



